import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';

import { UserService } from '../../angular-services/user.service';
import { User } from '../../classes/user';

@Component({
  selector: 'app-edit-user-form-admin',
  templateUrl: './edit-user-form.component.html',
  styleUrls: ['./edit-user-form.component.css']
})
export class EditUserFormComponent implements OnInit {

  @Output() userName = new EventEmitter<string>();
  formEditUser: FormGroup;
  user: User = new User();
  alert: any = { type: '', message: '', show: false };
  showPassword: boolean = false;

  constructor(private userService: UserService, private route: ActivatedRoute) { }

  ngOnInit() {
    this.initForm();
    this.getUser();
  }

  initForm() {
    this.formEditUser = new FormGroup({
      user: new FormControl('', Validators.required),
      password: new FormControl('', Validators.required),
      status: new FormControl('', Validators.required),
    });
  }

  getUser() {
    let key = this.route.snapshot.paramMap.get('id');
    this.userService.getUser(key).subscribe(
      response => {
        if (response !== undefined) {
          this.user = response;
          this.userName.emit(this.user.user);
          this.loadForm();
        } else {
          this.alert.type = 'danger';
          this.alert.message = 'Houve algum erro ao salvar os dados!';
          this.alert.show = true;
        }
      }
    )
  }

  loadForm() {
    this.formEditUser.patchValue({
      user: this.user.user,
      status: this.user.status
    });
  }

  save() {
    if (this.formEditUser.valid) {
      let key = this.route.snapshot.paramMap.get('id');
      let data = this.formEditUser.value;
      if (data.status === "true") {
        data.status = true;
      } else {
        data.status = false;
      }
      this.userService.updateUser(key, data).subscribe(
        response => {
          if (response !== undefined) {
            this.alert.type = 'success';
            this.alert.message = 'Dados salvos com sucesso!';
            this.alert.show = true;
          } else {
            this.alert.type = 'danger';
            this.alert.message = 'Houve algum erro ao salvar os dados!';
            this.alert.show = true;
          }
        }
      )
    }
  }

  changeTypeInputPassword() {
    if (this.showPassword) {
      this.showPassword = false;
    } else {
      this.showPassword = true;
    }
  }

}
